@charset "UTF-8";
@keyframes menu-open {
  0% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(1);
    visibility: visible;
  }
}
@keyframes menu-close {
  0% {
    transform: scaleY(1);
    visibility: visible;
  }
  99% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(0);
    visibility: hidden;
  }
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.button {
  margin: 0;
}

button.button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

a.button,
button.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  color: #444444;
  border-radius: 0;
  border: 2px solid #444444;
  box-shadow: none;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  transition: 140ms all ease-in-out;
  padding: 14px;
  font-size: 17px;
  line-height: 20px;
}
a.button:hover,
button.button:hover {
  background-color: #444444;
  color: #ffffff;
}
a.button:active,
button.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
a.button:disabled,
button.button:disabled {
  background-color: #666666;
  cursor: default;
  pointer-events: none;
  user-select: none;
}
a.button--center,
button.button--center {
  width: 100%;
  align-self: center;
}
a.button--add,
button.button--add {
  border: none;
  padding: 16px;
  background-color: #63d1bb;
  color: #ffffff;
}
a.button--add:hover,
button.button--add:hover {
  background-color: #46c1ae;
}
a.button--add:active,
button.button--add:active {
  background-color: #46c1ae;
  color: rgba(255, 255, 255, 0.3);
}

a.button:not([href]) {
  background-color: #666666;
  cursor: default;
  pointer-events: none;
  user-select: none;
}

.input {
  display: flex;
  align-items: center;
  font: inherit;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  background: #ffffff;
  border: 1px solid #f7f7f7;
  border-radius: 0;
  margin: 0;
  width: 100%;
  cursor: text;
  outline: none;
  box-shadow: none;
  transition: 140ms all ease-in-out;
}
@media (max-width: 767px) {
