}
.input::placeholder {
  color: rgba(68, 68, 68, 0.6);
}
.input + svg {
  color: #000000;
}
.input:hover {
  border-color: #cdcdcd;
}
.input:focus {
  border: 2px solid #444444;
}
@media (max-width: 767px) {
  .input:focus {
    padding: 8.5px 0;
    padding-left: 13px;
    padding-right: 39px;
  }
}
@media (min-width: 768px) {
