    padding: 9px 0;
    padding-left: 17px;
    padding-right: 49px;
  }
}
.input:focus + svg {
  color: #63d1bb;
}
.input:focus:invalid, .input--invalid, .input--invalid:hover {
  border: 2px solid #ff8282;
}
@media (max-width: 767px) {
  .input:focus:invalid, .input--invalid, .input--invalid:hover {
    padding: 8.5px 0;
    padding-left: 13px;
    padding-right: 39px;
  }
}
