    padding: 9px 0;
    padding-left: 17px;
    padding-right: 49px;
  }
}
.input:focus:invalid + svg, .input--invalid + svg, .input--invalid:hover + svg {
  color: #ff8282;
}
.input:disabled, .input:read-only {
  opacity: 0.6;
  cursor: default;
}
.input--num::-webkit-inner-spin-button, .input--num::-webkit-outer-spin-button {
  appearance: none;
}
.input--textarea {
