.main-nav__toggle {
  cursor: pointer;
  font: inherit;
  display: block;
  background-color: transparent;
  border: none;
  margin: 0;
  right: 0;
  top: 0;
  padding: 0;
  aspect-ratio: 1/1;
  height: 100%;
  position: absolute;
  z-index: 2;
  transition: 140ms all ease-in-out;
  color: #000000;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle--closed::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #000000, 0 -6px 0 0 #000000;
}
.main-nav__toggle--opened::before, .main-nav__toggle--opened::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background-color: #000000;
  box-shadow: none;
}
.main-nav__toggle--opened::after {
  transform: rotate(45deg);
}
.main-nav__toggle--opened::before {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav__wrapper {
    width: 100%;
    position: absolute;
    top: 100%;
    right: 0;
    animation-duration: 0.2s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    transform-origin: 0 0;
    visibility: hidden;
    transform: scaleY(0);
  }
  .main-nav__wrapper--opened {
    animation-name: menu-open;
  }
  .main-nav__wrapper--closed {
    animation-name: menu-close;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
  }
}
@media (min-width: 1150px) {
  .main-nav__wrapper {
    display: grid;
    grid-template-columns: 3fr 1fr 3fr;
    grid-auto-flow: dense;
  }
  .main-nav__wrapper::before {
    content: " ";
    display: block;
    grid-column: 2/3;
  }
}

.main-nav__list {
  display: flex;
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
  }
  .main-nav__list + .main-nav__list {
    margin-top: -1px;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

@media (max-width: 767px) {
  .main-nav--no-js .main-nav__toggle {
    visibility: hidden;
  }
  .main-nav--no-js .main-nav__wrapper {
    visibility: visible;
    transform: scaleY(1);
    position: static;
    margin-left: -30px;
    width: calc(100% + 60px);
  }
}
