  }
}
@media (min-width: 768px) {
  .footer__copyright {
    justify-self: end;
  }
}
@media (min-width: 1150px) {
  .footer__copyright {
    margin-top: 2px;
  }
}

.social__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
}

.social__link {
  width: 28px;
  height: 22px;
  position: relative;
  display: block;
  transition: 140ms all ease-in-out;
  background-color: transparent;
  color: #666666;
}
.social__link:hover {
  color: #63d1bb;
}
.social__link:active {
  color: rgba(99, 209, 187, 0.3);
}

.social__link svg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.copyright__link {
  font: inherit;
  color: #000000;
  text-decoration: none;
  transition: 140ms all ease-in-out;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
}
.copyright__link:hover .copyright__link-icon {
  color: #46c1ae;
}
.copyright__link:active .copyright__link-icon {
  color: rgba(70, 193, 174, 0.3);
}

.copyright__link-icon {
  vertical-align: middle;
  color: #63d1bb;
  margin-left: 27px;
}

